a {
  text-decoration: none;
}

button {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

.App {
  text-align: center;
  background: darkslategray;
  height: 100vh;
  display: flex;
  align-items: center;
}

.App-logo {
  height: 5vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 1vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.rqm-button {
  margin: 0;
  padding: .5em 1em;
  width: auto;
  background: darkslategray;
  font-size: calc(4px + 2vmin);
  border: solid transparent;
  border-radius: 5px; 
  color: white;
  font-weight: 600;
  text-shadow: 1px 1px black;
}

#author {
  text-align: right;
  font-size: calc(8pt + 2vmin);
}

#new-quote {
  float: right;
}

#quote-box {
  margin: auto;
  padding: 1.5em;
  width: 50vw;
  background: white;
  border: solid transparent;
  border-radius: 5px;
}

#text {
  font-size: calc(12pt + 2vmin);
  font-weight: 500;
}

#tweet-quote {
  float: left;
}